<template>
  <div id="note" class="detail">
    <NoteSideBar :curNote="currentNote" @update:notes="val => notes = val"></NoteSideBar>
    <!--    <h1>notebookId : {{$route.query.notebookId}}</h1>
        <h1>noteId : {{$route.query.noteId}}</h1>-->

    <div class="note-detail">
      <div class="note-empty" v-show="!currentNote.id">请选择笔记</div>
      <div class="note-detail-ct" v-show="currentNote.id">
        <div class="note-bar">
          <span>创建日期：{{ currentNote.createdAtFriendly }}</span>
          <span>更新日期：{{ currentNote.updatedAtFriendly }}</span>
          <span>{{ statusText }}</span>
          <span class="iconfont icon-delete" @click="deleteNote"></span>
          <span class="iconfont icon-fullscreen" @click="isShowPreview=!isShowPreview"></span>
        </div>
        <div class="note-title">
          <input type="text" v-model:value="currentNote.title" @input="updateNote" @keypress="statusText='正在输入...'" placeholder="输入标题">
        </div>
        <div class="editor">
          <textarea v-show="isShowPreview" v-model:value="currentNote.content" @input="updateNote" @keypress="statusText='正在输入...'" placeholder="输入内容, 支持 markdown"></textarea>
          <div class="preview markdown-body" v-html="previewContent" v-show="!isShowPreview"></div>
<!--          <div class="preview markdown-body" v-show="!isShowPreview"></div>-->
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Auth from '@/api/auth'
import NoteSideBar from '@/components/NoteSideBar'
import Bus from '@/helpers/bus'

// 节流 npm 中的 lodash
import _ from 'lodash'
import Notes from '@/api/notes'

import MarkdownIt from 'markdown-it'

let md = new MarkdownIt()

export default {
  name: "NoteDetail",
  components: {
    NoteSideBar,
  },
  data() {
    return {
      currentNote: {},
      notes: [],
      statusText: '未改动',
      isShowPreview: true,
    }
  },
  created() {
    // 如果没有登录，点击笔记详情，自动跳转到登录页面
    Auth.getInfo().then(res => {
      if (!res.isLogin) {
        this.$router.push({path: '/login'})
      }
    })
    // 在组件刚创建的时候，就开始监听
    Bus.$on('update:notes', val => {
      this.currentNote = val.find(note => note.id === parseInt(this.$route.query.noteId)) || {}
    })
  },
  beforeRouteUpdate(to, from, next) {
    // console.log('beforeRouteUpdate')
    // console.log(to, from)
    this.currentNote = this.notes.find(note => note.id === parseInt(to.query.noteId)) || {}
    next()
  },
  computed: {
    previewContent(){
      return md.render(this.currentNote.content || '')
    }
  },
  methods: {
    // _.debounce 等我输入停止300毫秒的时候才更新保存
    updateNote: _.debounce(function(){
      console.log('111')
      Notes.updateNote(
        {noteId: this.currentNote.id},
        {title: this.currentNote.title, content: this.currentNote.content}).then(res => {
        this.statusText = '已保存'
      }).catch(err => {
        this.statusText = '保存出错'
      })
    },300),
    deleteNote(){
      Notes.deleteNote({noteId: this.currentNote.id}).then(res => {
        this.$message.success('已放入回收站')
        this.notes.splice(this.notes.indexOf(this.currentNote), 1)
        this.$router.replace({path: `/note`})
      })
    }
  }
}
</script>

<style lang="less" scoped>
@import "../assets/css/note-detail.less";

#note {
  display: flex;
  align-items: stretch;
  background-color: #fff;
  flex: 1;
}
</style>



